<template>
  <li class="book-box">
    <div class="book-image">
      <img
        :src="require('@/assets/images/books/' + getImageFilePath(book.title))"
        :alt="book.title"
      />
      <div v-if="book.isPublic" class="book-image-interface">
        <button class="button read-btn">Read Now</button>
      </div>
    </div>
    <div class="book-title">{{ book.title }}</div>
    <div class="book-author">{{ book.author }}</div>
    <div class="book-price">
      <span class="price">{{ book.price | asDollarsAndCents }}</span>
    </div>
    <button class="add-to-cart-btn button" @click="addToCart(book)">
      Add to Cart
    </button>
  </li>
</template>

<script>
export default {
  name: "categoryBookListItem",
  props: {
    book: {
      type: Object,
      required: true,
    },
  },
  methods: {
    addToCart(book) {
      this.$store.dispatch("addToCart", book);
    },
    getImageFilePath(bookName) {
      let name = bookName.toLowerCase();
      name = name.replace(/ /g, "+").replace(/[\\<>:"/|?*]/, "");
      return `${name}.jpg`;
    },
  },
};
</script>
