<template>
  <div class="checkout-page">
    <section class="checkout-page-body" v-if="cart.empty">
      <h1>Your Cart Is Empty</h1>
      <div class="empty-cart-button">
        <router-link
          v-if="$store.state.selectedCategoryName !== ''"
          class="button continue-button"
          :to="{
            name: 'category',
            params: { name: $store.state.selectedCategoryName },
          }"
        >
          CONTINUE SHOPPING
        </router-link>
        <router-link
          v-else
          class="button continue-button"
          :to="{
            name: 'category',
            params: { name: $store.state.categories[0].name },
          }"
        >
          CONTINUE SHOPPING
        </router-link>
      </div>
    </section>
    <section class="checkout-page-body" v-else>
      <h1>Checkout Information</h1>
      <form @submit.prevent="submitOrder">
        <div>
          <label for="name">Name</label>
          <input
            type="text"
            size="20"
            id="name"
            name="name"
            :style="[
              $v.name.$error
                ? {
                    'border-color': 'var(--warning-color)',
                    'background-color': 'var(--warning-background-color)',
                  }
                : {},
            ]"
            v-model.lazy="$v.name.$model"
          />
        </div>
        <template v-if="$v.name.$error">
          <span class="error" v-if="!$v.name.required">Name is required</span>
          <span class="error" v-else-if="!$v.name.minLength">
            Name must have at least
            {{ $v.name.$params.minLength.min }} letters
          </span>
          <span class="error" v-else-if="!$v.name.maxLength">
            Name can have at most
            {{ $v.name.$params.maxLength.max }} letters
          </span>
          <span class="error" v-else> An unexpected error occurred </span>
        </template>

        <div>
          <label for="address">Address</label>
          <input
            type="text"
            size="20"
            id="address"
            name="address"
            :style="[
              $v.address.$error
                ? {
                    'border-color': 'var(--warning-color)',
                    'background-color': 'var(--warning-background-color)',
                  }
                : {},
            ]"
            v-model.lazy="$v.address.$model"
          />
        </div>
        <template v-if="$v.address.$error">
          <span class="error" v-if="!$v.address.required"
            >Address is required</span
          >
          <span class="error" v-else-if="!$v.address.minLength">
            Address must have at least
            {{ $v.address.$params.minLength.min }} letters
          </span>
          <span class="error" v-else-if="!$v.address.maxLength">
            Address can have at most
            {{ $v.address.$params.maxLength.max }} letters
          </span>
          <span class="error" v-else> An unexpected error occurred </span>
        </template>

        <div>
          <label for="phone">Phone</label>
          <input
            class="textField"
            type="text"
            size="20"
            id="phone"
            name="phone"
            :style="[
              $v.phone.$error
                ? {
                    'border-color': 'var(--warning-color)',
                    'background-color': 'var(--warning-background-color)',
                  }
                : {},
            ]"
            v-model.lazy="$v.phone.$model"
          />
        </div>
        <template v-if="$v.phone.$error">
          <span class="error" v-if="!$v.phone.required">Phone is required</span>
          <span class="error" v-else-if="!$v.phone.phone"
            >Please enter a valid phone number</span
          >
        </template>

        <div>
          <label for="email">Email</label>
          <input
            type="text"
            size="20"
            id="email"
            name="email"
            :style="[
              $v.email.$error
                ? {
                    'border-color': 'var(--warning-color)',
                    'background-color': 'var(--warning-background-color)',
                  }
                : {},
            ]"
            v-model.lazy="$v.email.$model"
          />
        </div>
        <template v-if="$v.email.$error">
          <span class="error" v-if="!$v.email.required">
            Email is required
          </span>
          <span class="error" v-else> Please enter a valid email </span>
        </template>

        <div>
          <label for="ccNumber">Credit Card</label>
          <input
            type="text"
            size="20"
            id="ccNumber"
            name="ccNumber"
            :style="[
              $v.ccNumber.$error
                ? {
                    'border-color': 'var(--warning-color)',
                    'background-color': 'var(--warning-background-color)',
                  }
                : {},
            ]"
            v-model.lazy="$v.ccNumber.$model"
          />
        </div>
        <template v-if="$v.ccNumber.$error">
          <span class="error" v-if="!$v.ccNumber.required">
            Credit card number is required
          </span>
          <span class="error" v-else>
            Please enter a valid credit card number
          </span>
        </template>

        <div class="expire-date">
          <div>Exp Date</div>
          <div class="expire-date-group">
            <select v-model="ccExpiryMonth" class="month">
              <option
                v-for="(month, index) in months"
                :key="index"
                :value="index + 1"
              >
                {{ month }} ({{ index + 1 }})
              </option>
            </select>
            <div>/</div>
            <select v-model="ccExpiryYear" class="year">
              <option
                v-for="(year, index) in 15"
                :key="index"
                :value="yearFrom(index)"
              >
                {{ yearFrom(index) }}
              </option>
            </select>
          </div>
        </div>
        <div class="line-sep"></div>
        <div class="charge-boxes">
          <div class="charge-box">
            <span class="subtitle">Subtotal:</span
            ><span>{{ $store.state.cart.subtotal | asDollarsAndCents }}</span>
          </div>
          <div class="charge-box">
            <span class="subtitle">Shipping:</span
            ><span>{{ $store.state.cart.surcharge | asDollarsAndCents }}</span>
          </div>
          <div class="line-sep line-sep-charge"></div>
          <div class="charge-box">
            <span class="subtitle">You Pay:</span
            ><span>{{
              ($store.state.cart.subtotal + $store.state.cart.surcharge)
                | asDollarsAndCents
            }}</span>
          </div>
          <div class="charge-box button-box">
            <input
              type="button"
              name="reset"
              class="button clear-button"
              @click="clear"
              :disabled="checkoutStatus == 'PENDING'"
              value="Clear Form"
            />
            <input
              type="submit"
              name="submit"
              class="button checkout-button"
              :disabled="checkoutStatus == 'PENDING'"
              value="Complete Purchase"
            />
          </div>
          <section v-show="checkoutStatus != ''" class="checkoutStatusBox">
            <div v-if="checkoutStatus == 'ERROR'" class="error">
              Please fix the problems above and try again
            </div>

            <div v-else-if="checkoutStatus == 'PENDING'">Processing...</div>

            <div v-else-if="checkoutStatus == 'OK'">Order placed...</div>

            <div v-else class="error">
              <!-- "checkoutStatus == 'SERVER_ERROR'" -->
              An unexpected error occurred, please try again
            </div>
          </section>
        </div>
      </form>
    </section>
  </div>
</template>

<script>
import {
  required,
  email,
  minLength,
  maxLength,
} from "vuelidate/lib/validators";

import isCreditCard from "validator/lib/isCreditCard";
import isMobilePhone from "validator/lib/isMobilePhone";

const phone = (value) => isMobilePhone(value, "en-US");
const creditCard = (value) => isCreditCard(value);

export default {
  data() {
    return {
      name: "",
      address: "",
      phone: "",
      email: "",
      ccNumber: "",
      ccExpiryMonth: new Date().getMonth() + 1,
      ccExpiryYear: new Date().getFullYear(),
      checkoutStatus: "",
    };
  },
  validations: {
    name: {
      required,
      minLength: minLength(4),
      maxLength: maxLength(45),
    },
    address: {
      required,
      minLength: minLength(4),
      maxLength: maxLength(45),
    },
    phone: {
      required,
      phone,
    },
    email: {
      required,
      email,
    },
    ccNumber: {
      required,
      creditCard,
    },
  },

  computed: {
    cart() {
      return this.$store.state.cart;
    },
    months() {
      return [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December",
      ];
    },
  },

  methods: {
    submitOrder() {
      console.log("Submit order");
      this.$v.$touch(); // Ensures validators always run
      if (this.$v.$invalid) {
        this.checkoutStatus = "ERROR";
      } else {
        let customer = {
          customerName: this.name,
          address: this.address,
          phone: this.phone,
          email: this.email,
          ccNumber:
            "*".repeat(this.ccNumber.length - 4) + this.ccNumber.slice(-4),
          ccExpiryMonth: this.ccExpiryMonth,
          ccExpiryYear: this.ccExpiryYear,
        };
        let cart = this.$store.state.cart;
        this.$store.commit("SET_ORDER_DETAILS", { customer, cart });
        this.checkoutStatus = "PENDING";
        setTimeout(() => {
          this.$store
            .dispatch("placeOrder", {
              name: this.name,
              address: this.address,
              phone: this.phone,
              email: this.email,
              ccNumber: this.ccNumber,
              ccExpiryMonth: this.ccExpiryMonth,
              ccExpiryYear: this.ccExpiryYear,
            })
            .then(() => {
              this.checkoutStatus = "OK";
              this.$router.push({ name: "confirmation" });
            })
            .catch((reason) => {
              this.$store.commit("CLEAR_ORDER_DETAILS");
              this.checkoutStatus = "SERVER_ERROR";
              console.log("Error placing order", reason);
            });
        }, 1000);
      }
    },

    /* NOTE: For example yearFrom(0) == 2021 */
    yearFrom(index) {
      return new Date().getFullYear() + index;
    },

    clear() {
      this.name = "";
      this.address = "";
      this.phone = "";
      this.email = "";
      this.ccNumber = "";
      this.checkoutStatus = "";
      this.$v.$reset();
    },
  },
};
</script>

