<template>
  <div class="confirmation-table">
    <ul>
      <template v-for="item in $store.state.orderDetails.lineItems">
        <li :key="item.book.bookId">
          <div class="book-image">
            <img
              :src="
                require('@/assets/images/books/' +
                  getImageFilePath(item.book.title))
              "
              :alt="item.book.title"
            />
          </div>
          <div class="book-title">{{ item.book.title }}</div>
          <div class="cart-book-quantity">
            <span class="quantity">{{ item.quantity }}</span>
          </div>
          <div class="book-price">
            {{ item.book.price | asDollarsAndCents }}
          </div>
          <div class="line-sep" :key="item.book.bookId"></div>
        </li>
      </template>
    </ul>
  </div>
</template>

<script>
export default {
  name: "ConfirmationTable",
  methods: {
    getImageFilePath(bookName) {
      let name = bookName.toLowerCase();
      name = name.replace(/ /g, "+").replace(/[\\<>:"/|?*]/, "");
      return `${name}.jpg`;
    },
  },
};
</script>

