import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import "@/assets/css/global.css";
import store from "./store";
import { CART_STORAGE_KEY } from "./store";
import Vuelidate from "vuelidate";
import TreeView from "vue-json-tree-view";
Vue.config.productionTip = false;

Vue.use(Vuelidate);
Vue.use(TreeView);

const PriceFormatter = new Intl.NumberFormat("en-US", {
  style: "currency",
  currency: "USD",
  minimumFractionDigits: 2,
});

Vue.filter("asDollarsAndCents", function (cents) {
  return PriceFormatter.format(cents / 100.0);
});

Vue.filter("asUSPhone", function (phone) {
  return (
    "(" +
    phone.substring(0, 3) +
    ")-" +
    phone.substring(3, 6) +
    "-" +
    phone.slice(-4)
  );
});

Vue.filter("asCCNumber", function (ccNumber) {
  if (ccNumber.length === 15) {
    return (
      ccNumber.substring(0, 4) +
      " " +
      ccNumber.substring(4, 10) +
      " " +
      ccNumber.slice(-4)
    );
  }
  return (
    ccNumber.substring(0, 4) +
    " " +
    ccNumber.substring(4, 8) +
    " " +
    ccNumber.substring(8, 12) +
    " " +
    ccNumber.slice(-4)
  );
});

Vue.filter("asDate", function (dateSecond) {
  let date = new Date(dateSecond);
  return (
    date.getMonth() +
    1 +
    "-" +
    date.getDate() +
    "-" +
    date.getFullYear() +
    " " +
    date.getHours() +
    ":" +
    date.getMinutes() +
    ":" +
    date.getSeconds()
  );
});

Vue.directive("click-outside", {
  bind: function (el, binding, vnode) {
    el.clickOutsideEvent = function (event) {
      // here I check that click was outside the el and his children
      if (!(el == event.target || el.contains(event.target))) {
        // and if it did, call method provided in attribute value
        vnode.context[binding.expression](event);
      }
    };
    document.body.addEventListener("click", el.clickOutsideEvent);
  },
  unbind: function (el) {
    document.body.removeEventListener("click", el.clickOutsideEvent);
  },
});

new Vue({
  router,
  store,
  render: function (h) {
    return h(App);
  },
  created() {
    const cartString = localStorage.getItem(CART_STORAGE_KEY);
    if (cartString) {
      const shoppingCart = JSON.parse(cartString);
      this.$store.commit("SET_CART", shoppingCart);
    }
  },
}).$mount("#app");
