<template>
  <div class="header-hamburger">
    <i
      class="fas fa-bars hamburger-btn"
      v-click-outside="closeMenu"
      @click="toggleMenu"
      ref="hamburger"
    ></i>
    <ul :class="{ unfold: meunOpened }">
      <li class="menu-title">Account</li>
      <li>Information</li>
      <li>Orders</li>
      <li>Shopping Cart</li>
      <li class="menu-title">Categories</li>
      <template v-for="category in $store.state.categories">
        <router-link
          :key="category.categoryId"
          :to="{ name: 'category', params: { name: category.name } }"
          @click.native="toggleMenu"
          tag="li"
          >{{ category.name }}</router-link
        >
      </template>
    </ul>
  </div>
</template>

<script>
export default {
  name: "HeaderHamburger",
  data: function () {
    return {
      meunOpened: false,
    };
  },
  methods: {
    toggleMenu() {
      this.meunOpened = !this.meunOpened;
    },
    closeMenu() {
      this.meunOpened = false;
    },
  },
};
</script>

